.th-gfont-popover {
	&:not(.is-mobile) {
		&.is-bottom {
			z-index: 9999999;
		}
	}

	.components-popover__content {
		overflow-y: hidden;

		.components-menu-group {
			padding: 7px;
		}
		
		.components-popover__items {
			max-height: 300px;
			overflow-y: auto;

			button {
				&.is-selected {
					background: #f8f9f9;
				}

				i {
					margin-right: 5px;
				}
			}
		}
	}
}

.th-gfont-control {
	.components-dropdown {
		display: flex;
	}

	.th-gfont-button {
		background: transparent;
		position: relative;
		width: 100%;
		height: auto;
		line-height: 28px;
		padding: 0 12px 2px;
		box-shadow: 0 0 0 transparent;
		transition: box-shadow .1s linear;
		border-radius: 4px;
		border: 1px solid #8d96a0;

		&.is-button {
			background: transparent;
		}

		&:hover {
			background: #fafafa;
			border-color: #999;
			box-shadow: inset 0 -1px 0 #999;
			color: #23282d;
			text-decoration: none;
		}

		&:focus {
			border: 1px solid black;
		}

		&:after {
			content: "";
			pointer-events: none;
			display: block;
			width: 0;
			height: 0;
			border-left: 3px solid transparent;
			border-right: 3px solid transparent;
			border-top: 5px solid currentColor;
			margin-left: 4px;
			margin-right: 2px;
			right: 8px;
			top: 12px;
			position: absolute;
		}
	}

	.o-control-panel-control {
		padding: 0;
	}
}

.th-gfont-control .components-dropdown {
    display: flex;
}

.th-gfont-control .th-gfont-button {
    background: transparent;
    border: 1px solid #8d96a0;
    border-radius: 4px;
    box-shadow: 0 0 0 transparent;
    height: auto;
    line-height: 28px;
    padding: 0 12px 2px;
    position: relative;
    transition: box-shadow .1s linear;
    width: 100%;
}
.th-gfont-control .th-gfont-button:after {
    border-left: 3px solid transparent;
    border-right: 3px solid transparent;
    border-top: 5px solid;
    content: "";
    display: block;
    height: 0;
    margin-left: 4px;
    margin-right: 2px;
    pointer-events: none;
    position: absolute;
    right: 8px;
    top: 12px;
    width: 0;
}
@media ( max-width: 782px ) {
	.th-gfont-control {
		.th-gfont-button {
			padding: 5px 24px 5px 8px;
	
			&:after {
				top: 16px;
			}
		}
	}
}